.mainDiv {
  text-align: right;
  margin-top: 4%;
  direction: rtl;
  font-family: Arial, Arial, Helvetica, sans-serif;
  background-color: #ffffff;
  max-width: 600px;
  margin: auto;
  margin-bottom: 4%;
  margin-right: 3%;
}
.body-h3{
  font: normal normal 600 14px/18px Assistant;
letter-spacing: 0.09px;
color: #324483;
opacity: 1;
}
.body-h5{
  font: normal normal normal 12px/22px Assistant;
letter-spacing: 0px;
color: #717480;
opacity: 1;
}
.body-button{
  width: 70px;
height: 21px;
background: #3D4257 0% 0% no-repeat padding-box;
border-radius: 8px;
opacity: 1;
}
.go-to-linkedin-btn{
  position: relative;
  background-color:rgb(61, 66, 87);
  color: white;
  width: 90%;
  padding: 10px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 3%;
  border: none !important;
  outline:none !important;
}
.footer2{
  width: 95%;
  margin: auto;
}