.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container1{
  background-color: #3d4257;
  color: white;
  direction: rtl;
  align-items: center;
  text-align: center;
  object-fit:contain;
}

.main-btn{
  background-color:rgb(61, 66, 87);
  color: rgb(226, 227, 230);
  width: 60%;
  padding: 15px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 3%;
  border: none !important;
  outline:none !important;
  
}